%unit-fight-animation-delta-health {
    display: block;
    left: 50%;
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 100000; // just very big number to be upper then others
}

.unit_fight_animation__delta_health__aggressor {
    @extend %unit-fight-animation-delta-health;

    left: 25%;
}

.unit_fight_animation__delta_health__defender {
    @extend %unit-fight-animation-delta-health;

    left: 75%;
}
