@import "/www/css/service";

.client_skirmish_map_item {
    background-color: $dark-blue;
    padding: 8px 16px;
}

.client_skirmish_map_item__main_content {
    @extend %ui-header-text;

    align-items: center;
    display: flex;
    flex-flow: row nowrap;
    font-size: 20px;
    gap: 8px 8px;
    justify-content: space-between;
}

.client_skirmish_map_item__left,
.client_skirmish_map_item__right {
    align-items: flex-start;
    display: flex;
    flex-direction: row;
    gap: 0 8px;
    justify-content: flex-start;
}

.client_skirmish_map_item__left {
    // overflow: hidden;
    // outline: 0;
    flex-grow: 1;

    // padding-left: 4px;
}

.client_skirmish_map_item__right {
    flex-shrink: 0;
    justify-content: flex-end;
}

/*
.client_skirmish_map_item__player_count {
    display: block;
    flex-shrink: 0;
}
*/

.client_skirmish_map_item__map_name {
    // @extend %ellipsis;
    @extend %ui-header-text;

    display: block;
    text-align: left;
    word-break: break-word;
}

.client_skirmish_map_item__show_map {
    @extend %ui-header-text;

    background-color: transparent;
    border: 0;
    display: block;
    margin: 0;
    padding: 0;
}

.client_skirmish_map_item__map_preview_wrapper {
    padding-top: 8px;
    width: 100%;
}

.client_skirmish_map_item__map_preview {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: center;
}
