%client-game-unit-fight-field-main {
    background-position: right top;
    background-repeat: repeat;
    height: 200%; // use too match height to prevent empty space
    image-rendering: pixelated;
    position: absolute;
    right: 0;
    top: 0;
    transform-origin: right top;
    width: 100%;
    z-index: 1;
}

%client-game-unit-fight-field,
.client_game_unit_fight_field {
    height: 100%;
    overflow: hidden;
    position: relative;
    width: 100%;
}

.client_game_unit_fight_field__right {
    @extend %client-game-unit-fight-field;

    transform: scaleX(-1);
}

.client_game_unit_fight_field_image {
    display: block;
    image-rendering: pixelated;
    position: absolute;
    right: 0;
    top: 0;
    transform-origin: right top;
    z-index: 0;
}
