@import "/www/css/var";

.client_game_unit_fight__page {
    background-color: transparent;
    background-image: none;
    outline: none;
    z-index: $unit-fight-animation-z-index;
}

.client_game_unit_fight__wrapper {
    box-shadow: 0 0 10px 5px #000;
    display: block;
    height: 100%;
    margin: 0 auto;
    position: relative;
    transform: translate3d(0, 110%, 0);
    z-index: $mame-popup-overlay-z-index + 1;
}

%client-game-unit-fight-side {
    box-sizing: border-box;
    display: block;
    height: calc(100% - $bottom-status-bar-height);
    overflow: hidden;
    position: absolute;
    text-align: center;
    top: 0;
    width: 50%;
    z-index: 1;
}

.client_game_unit_fight__left {
    @extend %client-game-unit-fight-side;

    border-right: 1px solid #000;
    left: 0;
}

.client_game_unit_fight__right {
    @extend %client-game-unit-fight-side;

    border-left: 1px solid #000;
    right: 0;
}

.client_game_unit_fight__ground {
    height: 100%;
    position: relative;
    transform: translate(0, 0) scale(1.01);
    width: 100%;
    z-index: 1;
}

.client_game_unit_fight__unit_animation {
    align-items: center;
    display: flex;
    flex-direction: row;
    height: calc(100% - $bottom-status-bar-height);
    justify-content: center;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 2;
}

.client_game_unit_fight__unit_animation_scaled {
    display: block;
    position: relative;
}

.client_game_unit_fight__bottom_bar {
    bottom: 0;
    height: $bottom-status-bar-height;
    left: 0;
    position: absolute;
    width: 100%;
    z-index: 1;
}
