@import "/www/css/var";

%client-game,
.client_game {
    background-position: center center;
    background-size: cover;
}

.client_game__menu_button {
    left: 16px;
    position: absolute;
    top: 16px;
    z-index: $game-ui-z-index;
}

.client_game__cell_stats {
    align-items: flex-end;
    bottom: $bottom-status-bar-height;
    display: flex;
    flex-direction: column;
    pointer-events: none;
    position: fixed;
    right: 0;
    z-index: $game-ui-z-index;
}

.client_game__bg_01 {
    @extend %client-game;

    background-image: url('./image/bg/bg-01.jpg');
}

