@import "/www/css/var";

.client_skirmish_map_section_header {
    align-items: center;
    background-color: $dark-blue;
    border-bottom: 2px solid $main-font-color;
    display: flex;
    flex-direction: row;
    gap: 0 16px;
    justify-content: flex-start;
    padding: 8px 16px;
    position: sticky;
    top: 0;
    z-index: 1;
}

.client_skirmish_map_section_header_next {
    background-color: transparent;
    border-bottom: 2px solid transparent;
}

.client_skirmish_map_section_header__image {
    display: block;
    height: 36px;
    image-rendering: pixelated;
    width: 36px;
}
