@import "/www/css/service";
@import "/www/css/var";

.client_game_unit_fight_bottom_bar {
    background-color: $dark-blue;
    display: block;
    height: $bottom-status-bar-height;
    overflow: hidden;
    position: relative;
    z-index: 1;
}

.client_game_unit_fight_bottom_bar__frame {
    @extend %ui-border-1;

    background-color: transparent;
    box-sizing: border-box;
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 5;
}

.client_game_unit_fight_bottom_bar__unit_list {
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    height: 100%;
    left: 0;
    place-items: stretch stretch;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: 4;
}

%client-game-unit-fight-bottom-bar-unit-image {
    display: block;
    height: $square-size * 2;
    image-rendering: pixelated;
    margin-top: 3px;
    width: $square-size * 2;
}

.client_game_unit_fight_bottom_bar__unit__image__aggressor {
    @extend %client-game-unit-fight-bottom-bar-unit-image;
}

.client_game_unit_fight_bottom_bar__unit__image__defender {
    @extend %client-game-unit-fight-bottom-bar-unit-image;

    transform: scaleX(-1);
}

%client-game-unit-fight-bottom-bar-unit {
    align-items: center;
    display: flex;
    flex-direction: row;
    gap: 0 8px;
    justify-content: center;
    width: 50%;
}

.client_game_unit_fight_bottom_bar__unit__aggressor {
    @extend %client-game-unit-fight-bottom-bar-unit;
}

.client_game_unit_fight_bottom_bar__unit__defender {
    @extend %client-game-unit-fight-bottom-bar-unit;
}
