@import "/www/css/var";

.bottom_status_bar {
    align-items: center;
    background-color: #7d7e99;
    border-top: 1px solid #ccc;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-shrink: 0;
    gap: 0 8px;
    justify-content: center;
    overflow: auto;
    padding: 4px;
    width: 100%;
}
