@import '/www/css/var';

.client_building_stats {
    align-items: center;
    background-color: #0008;

    // bottom: $bottom-status-bar-height;
    display: flex;
    flex-direction: row;
    image-rendering: pixelated;
    justify-content: center;
    padding: 4px 4px 3px;

    // position: fixed;
    // right: 0;
    // z-index: $game-ui-z-index;
}

.client_building_stats__building {
    display: block;
    height: 24px;
    image-rendering: pixelated;
    object-fit: cover;
    object-position: bottom center;
    width: 24px;
}

.client_building_stats__icon {
    display: block;
    flex-shrink: 0;
    height: 16px;
    margin-left: 4px;
    margin-top: 2px;
    width: 16px;

    &:first-child {
        margin-left: 0;
    }
}

.client_building_stats__text {
    // display: block;
    outline: none;
}
