@import '/www/css/var';

.client_unit_stats__wrapper {
    align-items: center;
    background-color: #0008;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    padding-left: 4px;
}

.client_unit_stats {
    align-items: center;

    // bottom: $bottom-status-bar-height;
    display: flex;
    flex-direction: row;
    image-rendering: pixelated;
    padding: 3px 4px 1px;
    place-content: center flex-start;
}

.client_unit_stats__level {
    padding-left: 28px;
}

.client_unit_stats__unit {
    display: block;
    image-rendering: pixelated;
}

.client_unit_stats__icon {
    display: block;
    flex-shrink: 0;
    height: 16px;
    margin-left: 4px;
    margin-top: 2px;
    width: auto;

    &:first-child {
        margin-left: 0;
    }
}

.client_unit_stats__text {
    // display: block;
    outline: none;
}
