@import "/www/css/service";

.client_settings__content {
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    gap: 16px 0;
    justify-content: flex-start;
}

.client_settings__header {
    @extend %ui-header-text;

    display: block;
    font-size: 22px;
    margin: 0;
    padding: 24px 0 8px;
    text-align: left;

    &:first-child {
        padding-top: 0;
    }
}
